import React from 'react';
import config from '../../site-config';
import Subscription from '../components/subscription/subscription';
import styles from './index.module.scss';
import Cover from '../components/cover/cover';
import coffeeImage from '../assets/cover/coffee.jpeg';

class ServicesPage extends React.Component {
    constructor(props) {
        super(props);

        this.closeSubscription = this.closeSubscription.bind(this);
        this.state = {
            showSubscriptionBox: config.isSubscriptionEnabled
        }
    }
    closeSubscription() {
        this.setState({
            showSubscriptionBox: false
        });
    }
    render() {
        return (
            <div className={styles.index}>
                <Cover backgroundUrl={coffeeImage}></Cover>
            </div>
        )
    }
}

export default ServicesPage;



